const { ProfileService } = require('../../../services');

module.exports = async (req, res) => {
  try {
    const users = await ProfileService.findAllUsersByName(
      req.query.name,
      req.query.page,
    );
    res.success({
      ...users,
    });
  } catch (err) {
    res.error({
      message: 'api.user.find-all.fail',
    });
  }
};
